module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">children</td><td style=\"padding-bottom: 1.8rem\">node</td><td style=\"color: #d53700; padding-bottom: 1.8rem\">required</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">The child content for the table consisting of either a TableHeader and/or a TableBody.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">disableStripes</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Whether or not the rows should be zebra striped.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">paddingStyle</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'none'</td><td style=\"padding-bottom: 1.8rem\">Indicates the desired divider styles to apply to a row and its children.\nOne of <code style=\"white-space: pre-wrap\">&#39;none&#39;</code>, <code>&#39;standard&#39;</code>, <code>&#39;compact&#39;</code>.</td></tr></tbody></table>"